.Home {
  height: 100vh;
}

.left-filter-bar {
  position: fixed;
  left: 24px;
  top: 21vh;
  width: 40px;
  font-size: 0;
}
.filter-handle {
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.6);
}
.filter-handle:first-child {
  padding-top: 10px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  border-bottom: 1px solid rgba(171, 171, 171, 0.85);
}
.filter-handle:last-child {
  padding-bottom: 10px;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top: 1px solid rgba(171, 171, 171, 0.85);
}
.filter-handle.selected {
  background-color: rgba(2, 132, 139, 0.6);
}

.filter-handle span {
  display: inline-block;
  height: 50px;
  width: 40px;
  background: center center/30px no-repeat
}
.filter-handle .scenic {
  background-image: url(./images//scenic.png);
}
.filter-handle .parking {
  background-image: url(./images/parking.png);
}
.filter-handle .toilet {
  background-image: url(./images/toilet.png);
}
.filter-handle.selected .scenic {
  background-image: url(./images//scenic-selected.png);
}
.filter-handle.selected .parking {
  background-image: url(./images/parking-selected.png);
}
.filter-handle.selected .toilet {
  background-image: url(./images/toilet-selected.png);
}

.main-nav {
  display: flex;
  flex-direction: row;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 1000;
  height: 26px;
  width: 100vw;
  background-color: #fff;
  border-top: 1px solid #e73c2f;
}

.nav-button {
  flex: 1;
  text-align: center;
  line-height: 26px;
}

.nav-button span {
  display: inline-block;
  color: black;
  font-size: 12px;
}

.nav-button.main {
  flex: 0;
  width: 40px;
}
.nav-button.main span {
  font-size: 0;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  margin-top: -16px;
  background: #e73c2f url(./images/main.png) center center/30px no-repeat;
}
.scenic-change span {
  padding: 0 22px;
  background: url(./images/menu.png) left center/16px no-repeat;
}
.follow-wechat span {
  padding: 0 22px;
  background: url(./images/wechat.png) left center/16px no-repeat;
}

.scenic-list {
  position: fixed;
  left: 10px;
  bottom: 27px;
}
.scenic-list ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.scenic-list li {
  background-color: rgba(231, 60, 47, 0.65);
  height: 28px;
  line-height: 28px;
  font-size: 14px;
  text-align: center;
  padding: 0 10px;
  border-bottom: 1px solid #e73c2f;
  border-top: 1px solid #f27e76;
}
.scenic-list li:first-child {
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  border-top-width: 0px;
}
.scenic-list li:last-child {
  border-bottom-width: 0px;
}

.scenic-list li a {
  text-decoration: none;
  color: #fff;
  display: inline-block;
  width: 100%;
  height: 100%;
}


.jixiangwu {
  position: absolute;
  top: 8px;
  left: 50%;
  width: 192px;
  height: 62px;
  margin-left: -96px;
  background: url(./images/jixiangwu.png) center center/100% no-repeat;
}
.gongzonghaoQR {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 10px;
  border-radius: 10px;
  width: 250px;
  height: 250px;
  margin-left: -125px;
  margin-top: -125px;
  background: rgba(255, 255, 255, 0.6);
  /* background-color: red; */
  box-shadow:0px 3px 3px #c8c8c8 ;
}
.gongzonghaoQR img {
  width: 250px;
  height: 250px;
}
.gongzonghaoQR .closeQR {
  position: absolute;
  right: -20px;
  top: -20px;
  display: inline-block;
  padding: 10px;
}
.gongzonghaoQR .closeQR span {
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background: rgb(255, 255, 255) url(./images/close.png) center center/24px no-repeat;
  opacity: 0.6;
}

.marker {
  width: 48px;
  height: 48px;
  background: center center/100% no-repeat;
}
.marker.active {
  transform: scale(1.5);
}
.marker.scenic {
  background-image: url(./images/scenic-marker.png);
}
.marker.cate {
  background-image: url(./images/cate-marker.png);
}
.marker.hotel {
  background-image: url(./images/hotel-marker.png);
}
.marker.parking {
  background-image: url(./images/parking-marker.png);
}
.marker.toilet {
  background-image: url(./images/toilet-marker.png);
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
