.scenic-card {
  position: fixed;
  left: 10px;
  right: 10px;
  bottom: -76px;
  border-radius: 16px;
  background-color: rgba(255, 255, 255, 0.75);
  padding: 10px;
  transition: bottom 0.5s ease-in-out .125s;;
}
.scenic-card.show {
  bottom: 50px;
}

.scenic-intro {
  padding: 0 25px;
  position: relative;
  display: flex;
  flex-direction: row;
}
.scenic-avator {
  width: 40px;
  height: 40px;
  background: #e73c2f center center/30px no-repeat;
  border-radius: 6px;
  margin-right: 12px;
}

.scenic-desc {

}
.scenic-desc .scenic-name {
  font-size: 14px;
  line-height: 14px;
  margin: 2px 0 4px;
  color: #333333;
}
.scenic-desc .scenic-address {
  display: inline-block;
  padding-left: 18px;
  font-size: 12px;
  line-height: 14px;
  color: rgba(51, 51, 51, 0.7);
  background: url(./images/location.png) left center/14px no-repeat;
}

.scenic-services {
  display: flex;
  flex-direction: row;
  padding: 24px 25px 0;
}
.scenic-services .button-wrap {
  flex: 1;
  border-bottom: 1px solid #b3b8bf;
  text-align: center;
}
.scenic-services .button-wrap .btn {
  display: inline-block;
  width: 80px;
  height: 28px;
  margin-bottom: 18px;
  border-radius: 14px;
  background: #e73c2f;
  text-align: center;
  font-size: 12px;
  line-height: 28px;
  text-decoration: none;
  color: white;
}

.scenic-services-list {
  display: flex;
  flex-direction: row;
  height: 80px;
  margin-top: 12px;
}
.scenic-services-list .prev-service {
  width: 42px;
  background: url(./images/left.png) center center/30px no-repeat;
}
.scenic-services-list .next-service {
  width: 42px;
  background: url(./images/right.png) center center/30px no-repeat;
}
.service-intro {
  flex: 1;
  display: flex;
  flex-direction: row;
}
.scenic-services-total {
  width: 60px;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  text-align: center;
  color: #bdbdbd;
  position: absolute;
  font-weight: bold;
  top: 0px;
  right: 20px;
  background-color: #f9f9f9;
}
.service-intro .service-intro-img {
  width: 50px;
  margin-right: 10px;
  text-align: center;
}
.service-intro .service-intro-img img {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  margin-bottom: 6px;
}
.service-intro .service-intro-img span {
  font-size: 10px;
  color: #787d84;
}
.service-intro .service-intro-desc {
  flex: 1;
  height: 80px;
  overflow-y: scroll;
  margin-right: 10px;
}
.service-intro .service-intro-desc p {
  margin: 0;
  font-size: 8px;
}
.service-intro .service-intro-desc .triangle {
  display: inline-block;
  width: 8px;
  height: 8px;
  background: url(./images/triangle.png) center center/100% no-repeat;
  margin-right: 2px;
}
.service-intro .service-intro-pics {
  position: relative;
  width: 80px;
  background: #ccc;
}
.service-intro .service-intro-pics img {
  width: 100%;
  height: 100%;
}
.service-intro .service-intro-pics .navigation {
  display: inline-block;
  font-size: 0;
  height: 26px;
  width: 80px;
  background: #e73c2f;
  position: absolute;
  left: 0px;
  bottom: 0px;
  text-align: center;
  cursor: pointer;
}
.service-intro .service-intro-pics .navigation span {
  font-size: 12px;
  line-height: 26px;
  color: #fff;
  padding-left: 16px;
  background: url(./images/flight.png) left center/14px no-repeat;
}

